.pageIndex{
    width: 100%;
    background: rgba(222, 222, 222,255);
}
.content{
    width:20.8rem;
    height: 72.8rem;
    background: url(../../../access/index/beijing.jpg);
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 auto;
    .content-vedio{
        width: 100%;
        height: 10rem;
    }
    .content-longon{
        width: 100%;
        height:9rem;
    }
    .content-huodong{
        width: 100%;
        height: 9.6rem;
        display: flex;
        align-items: flex-end;
        .btn_ddz{
            width: 15%;
            height: 1rem;
            margin-left: 25%;
            background: url(../../../access/index/yellowBtn.png);
            background-size: 100% 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 18px;
            color: #7b4e3c;
            cursor: pointer;
        }
        .btn_qhb{
            width: 15%;
            height: 1rem;
            margin-left: 20%;
            background: url(../../../access/index/yellowBtn.png);
            background-size: 100% 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 18px;
            color: #7b4e3c;
            cursor: pointer;
        }
    }
    .content-ddzds{
        width: 100%;
        height:12rem;
        display: flex;
        align-items: flex-end;
        .btn_ddz{
            width: 15%;
            height: 1rem;
            margin-left: 50%;
            background: url(../../../access/index/yellowBtn.png);
            background-size: 100% 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 18px;
            color: #7b4e3c;
            cursor: pointer;
        }
    }
    .rightFixed{
        position: fixed;
        border-radius: 5px;
        top: 20%;
        right: 0;
        width: 4rem;
        height: 10rem;
        // border: 1px solid rgb(173,108,41);
        color: black;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 5px;
        .rightFixed-font{
            display: flex;
            justify-content: center;
            align-items: center;
            height: 0.8rem;
            width: 80%;
            border: 1px solid rgb(173,108,41);
            border-radius: 10px;
            cursor: pointer;
            background: white;
            color: rgb(173,108,41);
        }
        .rightFixed-img{
            width: 80%;
            border: 1px solid rgb(173,108,41);
            border-radius: 10px 30px 10px 30px;
            overflow: hidden;
        }
    }
}
.bottom{
    height: 4rem;
    width: 20.8rem;
    margin: 0 auto;
    display: flex;
    align-items:center;
    .bottom-box1{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 70%;
        width: 30%;
    }
    .bottom-box2{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction: column;
        height:70%;
        width: 40%;
    }
    .bottom-box3{
        height: 70%;
        width:25%;
        margin-left: 5%;
        display: flex;
        justify-content: space-between;
        align-items: center;

    }
    
}


